        <div v-if="question.optionList">
          <!--<h1 class="section__title">-->
          <!--<span>选项</span>-->
          <!--<span class="section__subtitle">单/多选题的选项范围从2至20</span>-->
          <!--</h1>-->

          <div v-for="(option, optionIdx) in question.optionList"
               :key="optionIdx"
               class="section__question">
            <span class="el-radio">{{String.fromCharCode(optionIdx + 65) + '、'}}</span>
            <!--<el-input v-model="question.optionList[optionIdx]"/>-->
            <span>{{option.optionTitle}}</span>
          </div>
        </div>
      </div>
      <!--</div>-->

      <div class="section">
        <h1 class="section__title">共有{{table.total}}人参加</h1>
        <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th>姓名</th>
            <th>子女</th>
            <th>手机号</th>
            <th>参与时间</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item, idx) in table.list"
              :key="idx">
            <td>{{item.name}}</td>
            <td>{{item.childrenList.map(child => child.studentName).join(', ')}}</td>
            <td>{{item.phone}}</td>
            <td>{{item.participatedTime | substrTime}}</td>
            <td>
              <span class="oper"
                    @click="goRecord(item.id)">详情</span>
            </td>
          </tr></tbody>
        </table>
        </resize-table>
        <no-data v-show="!table.total"/>
        <paging :data="table"/>
