</template>

<script>
  export default {
    data () {
      return {
        nav: [{name: '调查问卷', path: 'go(-1)'}, {name: '查看', path: ''}],
        id: this.$route.params.id,
        surveyInfo: {},
        table: {
          url: '/survey/querySurveyRecordPage.do',
          query: {
            id: this.$route.params.id,
            pageNo: 1,
            pageSize: 10
          },
          list: [],
          total: 0
        }
      }
    },
    async mounted () {
      this.$search(this.table)
      this.surveyInfo = await this.$ajax({
        url: '/survey/getSurvey.do',
        query: {
          id: this.id
        }
      })
    },
    methods: {
      goRecord (recordId) {
        this.$router.push(`/student-work/survey/${this.id}/${recordId}`)
      }
    }
  }
</script>

